"use client"

import { useState, useRef, useEffect } from "react"
import { Copy, MapPin, ArrowLeft } from "lucide-react"
import { cn } from "@/lib/utils"

interface CookingInterfaceProps {
  recipe: {
    recipe_name: string
    ingredients: string[]
    instructions: string[]
  }
  onBack: () => void
}

export function CookingInterface({ recipe, onBack }: CookingInterfaceProps) {
  const [activeTab, setActiveTab] = useState<"ingredients" | "directions">("ingredients")
  const [checkedIngredients, setCheckedIngredients] = useState<Record<number, boolean>>({})
  const [currentStepIndex, setCurrentStepIndex] = useState(0)
  const [timerActive, setTimerActive] = useState<number | null>(null)
  const [timerSeconds, setTimerSeconds] = useState(0)
  const directionsContainerRef = useRef<HTMLDivElement>(null)
  const stepRefsRef = useRef<(HTMLDivElement | null)[]>([])

  // Extract timer from instruction text (e.g., "10 minutes" -> 600 seconds)
  const extractTimer = (text: string): number | null => {
    const timeMatch = text.match(/(\d+)\s*(?:minutes?|mins?|hours?|hrs?)/i)
    if (timeMatch) {
      const value = Number.parseInt(timeMatch[1])
      const unit = timeMatch[0].toLowerCase()
      if (unit.includes("hour")) return value * 3600
      if (unit.includes("minute") || unit.includes("min")) return value * 60
    }
    return null
  }

  // Format seconds to MM:SS
  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins.toString().padStart(2, "0")}:${secs.toString().padStart(2, "0")}`
  }

  // Timer effect
  useEffect(() => {
    let interval: NodeJS.Timeout
    if (timerActive !== null && timerSeconds > 0) {
      interval = setInterval(() => {
        setTimerSeconds((prev) => {
          if (prev <= 1) {
            setTimerActive(null)
            return 0
          }
          return prev - 1
        })
      }, 1000)
    }
    return () => clearInterval(interval)
  }, [timerActive, timerSeconds])

  // Get ingredients for current step
  const getStepIngredients = (step: string): string[] => {
    return recipe.ingredients.filter((ing) => {
      const ingName = ing.split(",")[0].toLowerCase()
      return step.toLowerCase().includes(ingName)
    })
  }

  // Use IntersectionObserver to track active step
  useEffect(() => {
    if (activeTab !== "directions") return

    const observer = new IntersectionObserver(
      (entries) => {
        // Find the entry with the highest intersection ratio
        let maxRatio = 0
        let maxIndex = 0
        
        entries.forEach((entry) => {
          if (entry.isIntersecting && entry.intersectionRatio > maxRatio) {
            const index = Number(entry.target.getAttribute("data-step-index"))
            if (!isNaN(index)) {
              maxRatio = entry.intersectionRatio
              maxIndex = index
            }
          }
        })
        
        if (maxRatio > 0.5) {
          setCurrentStepIndex(maxIndex)
        }
      },
      {
        root: null, // Use viewport instead of container
        threshold: 0.6,
        rootMargin: "0px"
      }
    )

    stepRefsRef.current.forEach((ref) => {
      if (ref) observer.observe(ref)
    })

    return () => observer.disconnect()
  }, [activeTab, recipe.instructions.length])

  const toggleAllIngredients = () => {
    const allChecked = recipe.ingredients.every((_, i) => checkedIngredients[i])
    if (allChecked) {
      setCheckedIngredients({})
    } else {
      setCheckedIngredients(Object.fromEntries(recipe.ingredients.map((_, i) => [i, true])))
    }
  }

  const copyIngredients = () => {
    const ingredientsList = recipe.ingredients.join("\n")
    navigator.clipboard.writeText(ingredientsList)
  }

  return (
    <div className="min-h-screen bg-neutral-50 flex flex-col">
      {/* Header */}
      <div className="sticky top-0 bg-white border-b border-neutral-200 px-4 py-4 sm:px-6 z-20">
        <div className="max-w-4xl mx-auto flex items-center justify-center gap-4 relative">
          <button 
            onClick={onBack} 
            className="absolute left-0 text-neutral-900 hover:text-neutral-600 transition-colors"
          >
            <ArrowLeft className="w-6 h-6" />
          </button>
          
          <div className="flex items-center gap-3">
            {/* <div className="bg-neutral-200 text-neutral-700 text-xs font-bold px-3 py-1.5 rounded-md uppercase tracking-wide">
              CheffGPT
            </div> */}
            <h1 className="text-xl sm:text-2xl font-bold text-neutral-900">{recipe.recipe_name}</h1>
          </div>
        </div>
      </div>

      {/* Tabs */}
      <div className="sticky top-[73px] bg-white border-b border-neutral-200 px-4 sm:px-6 py-3 z-10">
        <div className="max-w-4xl mx-auto flex items-center justify-center gap-8">
          <button
            onClick={() => setActiveTab("ingredients")}
            className={cn(
              "pb-2 text-lg font-medium transition-all relative",
              activeTab === "ingredients" ? "text-neutral-900" : "text-neutral-500 hover:text-neutral-700"
            )}
          >
            Ingredients
            {activeTab === "ingredients" && (
              <span className="absolute bottom-0 left-0 w-full h-0.5 bg-neutral-900 rounded-full" />
            )}
          </button>
          <button
            onClick={() => setActiveTab("directions")}
            className={cn(
              "pb-2 text-lg font-medium transition-all relative",
              activeTab === "directions" ? "text-neutral-900" : "text-neutral-500 hover:text-neutral-700"
            )}
          >
            Directions
            {activeTab === "directions" && (
              <span className="absolute bottom-0 left-0 w-full h-0.5 bg-neutral-900 rounded-full" />
            )}
          </button>
        </div>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-hidden">
        {activeTab === "ingredients" ? (
          <div className="px-4 sm:px-6 py-8 max-w-4xl mx-auto">
            {/* Header Section */}
            <div className="mb-6">
              <div className="flex items-end justify-between mb-2">
                <div>
                  <h2 className="text-2xl font-bold text-neutral-900">Get Ingredients</h2>
                  <p className="text-neutral-500 text-sm mt-1">
                    Select the ingredients you have missing to view nearby store options or add to your list.
                  </p>
                </div>
                <button
                  onClick={toggleAllIngredients}
                  className="text-sm font-semibold text-neutral-900 underline decoration-2 underline-offset-4 hover:text-neutral-600 whitespace-nowrap ml-4"
                >
                  Select All
                </button>
              </div>
            </div>

            {/* Ingredients Card */}
            <div className="bg-white rounded-3xl p-2 shadow-sm border border-neutral-100 mb-24">
              <div className="space-y-1">
                {recipe.ingredients.map((ingredient, idx) => (
                  <div
                    key={idx}
                    className="group flex items-center gap-4 p-4 rounded-2xl hover:bg-neutral-50 transition-colors cursor-pointer"
                    onClick={() => {
                      setCheckedIngredients((prev) => ({
                        ...prev,
                        [idx]: !prev[idx],
                      }))
                    }}
                  >
                    <div className="flex-1">
                      <p
                        className={cn(
                          "text-lg font-medium transition-all",
                          checkedIngredients[idx] ? "text-neutral-400 line-through" : "text-neutral-900"
                        )}
                      >
                        {ingredient}
                      </p>
                    </div>

                    {/* Checkbox */}
                    <div
                      className={cn(
                        "w-8 h-8 rounded-lg border-2 flex items-center justify-center transition-all",
                        checkedIngredients[idx]
                          ? "bg-neutral-900 border-neutral-900"
                          : "border-neutral-200 group-hover:border-neutral-300"
                      )}
                    >
                      {checkedIngredients[idx] && (
                        <svg className="w-5 h-5 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                        </svg>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Floating Action Buttons */}
            <div className="fixed bottom-8 left-1/2 -translate-x-1/2 z-40 flex gap-3">
              <button
                onClick={copyIngredients}
                className="bg-neutral-400 hover:bg-neutral-500 text-white font-semibold py-3 px-8 rounded-full shadow-lg flex items-center gap-2 transition-all"
              >
                Copy
              </button>
              <button className="bg-neutral-900 hover:bg-neutral-800 text-white font-semibold py-3 px-8 rounded-full shadow-lg flex items-center gap-2 transition-all">
                Near Stores
              </button>
            </div>
          </div>
        ) : (
          <div ref={directionsContainerRef} className="h-full overflow-y-scroll px-4 sm:px-6 py-8 max-w-4xl mx-auto snap-y snap-mandatory scroll-smooth">
            <div className="pb-32">
              {recipe.instructions.map((instruction, idx) => {
                const isActive = idx === currentStepIndex
                const stepIngredients = getStepIngredients(instruction)
                
                return (
                  <div
                    key={idx}
                    ref={(el) => {
                      stepRefsRef.current[idx] = el
                    }}
                    data-step-index={idx}
                    className={cn(
                      "transition-all duration-500 rounded-3xl overflow-hidden snap-center snap-always mb-6 last:mb-0",
                      "min-h-[calc(100vh-280px)] h-[calc(100vh-280px)]",
                      isActive 
                        ? "bg-white shadow-xl scale-100 opacity-100" 
                        : "bg-neutral-200 scale-95 opacity-50 cursor-pointer hover:opacity-70"
                    )}
                    onClick={() => {
                      if (!isActive && stepRefsRef.current[idx]) {
                        stepRefsRef.current[idx]?.scrollIntoView({ behavior: 'smooth', block: 'center' })
                      }
                    }}
                  >
                    <div className="p-8 h-full flex flex-col">
                      {/* Step Header */}
                      <div className="flex items-center gap-2 mb-4">
                        <div className={cn(
                          "px-3 py-1 rounded-lg flex items-center gap-2",
                          isActive ? "bg-neutral-100" : "bg-neutral-300"
                        )}>
                          <span className="font-bold text-neutral-900">Step {idx + 1}</span>
                          <span className="text-neutral-500">‚Üó</span>
                        </div>
                      </div>

                      {/* Step Ingredients Tags */}
                      {isActive && stepIngredients.length > 0 && (
                        <div className="flex flex-wrap gap-2 mb-6">
                          {stepIngredients.map((ing, i) => (
                            <span
                              key={i}
                              className="px-3 py-1 bg-neutral-100 rounded-lg text-sm text-neutral-600 font-medium"
                            >
                              {ing.split(",")[0]}
                            </span>
                          ))}
                        </div>
                      )}

                      {/* Instruction Text */}
                      <p 
                        className={cn(
                          "leading-relaxed font-medium transition-all",
                          isActive 
                            ? "text-3xl sm:text-4xl text-neutral-900" 
                            : "text-lg text-neutral-600 line-clamp-3"
                        )}
                      >
                        {isActive ? (
                          // Active step with timer highlighting
                          instruction.split(/(\d+\s*(?:minutes?|mins?))/gi).map((part, i) => {
                            if (part?.match(/\d+\s*(?:minutes?|mins?)/i)) {
                              const timerVal = extractTimer(part)
                              return (
                                <span
                                  key={i}
                                  onClick={(e) => {
                                    e.stopPropagation()
                                    if (timerVal) {
                                      setTimerActive(idx)
                                      setTimerSeconds(timerVal)
                                    }
                                  }}
                                  className="text-green-600 font-bold hover:underline cursor-pointer"
                                >
                                  {part}
                                </span>
                              )
                            }
                            return part
                          })
                        ) : (
                          // Inactive step - just show text
                          instruction
                        )}
                      </p>
                    </div>
                  </div>
                )
              })}
            </div>
          </div>
        )}
      </div>

      {/* Floating progress indicator that only shows on directions tab */}
      {activeTab === "directions" && (
        <div className="fixed bottom-8 left-1/2 -translate-x-1/2 z-40 w-full max-w-md px-4">
          <div className="bg-white/90 backdrop-blur-xl border border-neutral-200 shadow-2xl rounded-2xl p-4">
            <div className="flex items-center justify-between mb-3">
              <span className="text-sm font-semibold text-neutral-900">
                Step {currentStepIndex + 1} <span className="text-neutral-400">of {recipe.instructions.length}</span>
              </span>
              <span className="text-xs font-bold text-neutral-900">
                {Math.round(((currentStepIndex + 1) / recipe.instructions.length) * 100)}%
              </span>
            </div>
            
            {/* Segmented Progress Bar */}
            <div className="flex gap-1.5 h-3">
              {recipe.instructions.map((_, i) => (
                <div 
                  key={i}
                  className={cn(
                    "h-full flex-1 rounded-full transition-all duration-300",
                    i <= currentStepIndex ? "bg-neutral-900" : "bg-neutral-100"
                  )}
                />
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Timer Modal */}
      {timerActive !== null && timerSeconds > 0 && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white rounded-3xl p-8 text-center shadow-lg">
            <h3 className="text-2xl font-bold text-neutral-900 mb-6">Cooking Timer</h3>
            <div className="text-7xl font-bold text-neutral-900 mb-8 font-mono">{formatTime(timerSeconds)}</div>
            <div className="flex gap-4">
              <button
                onClick={() => {
                  setTimerActive(null)
                  setTimerSeconds(0)
                }}
                className="flex-1 px-6 py-3 bg-neutral-200 text-neutral-900 rounded-full font-semibold hover:bg-neutral-300 transition-colors"
              >
                Stop
              </button>
              <button
                onClick={() => setTimerSeconds(0)}
                className="flex-1 px-6 py-3 bg-neutral-900 text-white rounded-full font-semibold hover:bg-neutral-800 transition-colors"
              >
                Done
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
